<template>
    <v-container fluid>
        
        <v-app-bar app>

            <v-menu
                left
                bottom
                class="hidden-sm-and-up"
            >
                <template v-slot:activator="{ on }">
                <v-btn icon v-on="on" class="hidden-sm-and-up">
                    <v-app-bar-nav-icon></v-app-bar-nav-icon>
                </v-btn>
                </template>

                <v-list>
                <v-list-item
                    v-for="(item, index) in menuItems" :key="index"
                    @click="() => {}"
                >
                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item>
                </v-list>
            </v-menu>
            
            <v-toolbar-title>
                <router-link to="/" tag="span" style="cursor: pointer">
                {{ appTitle }}
                </router-link>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            
            <v-toolbar-items class="hidden-xs-only">
                
                <v-btn
                    text
                    :to="'/'"
                >
                    <v-icon left dark>home</v-icon>
                    Home
                </v-btn>

                <v-btn
                    text
                    :to="'/notas'"
                    v-if="estaActivo"
                >
                    <v-icon left dark>note</v-icon>
                    Notas
                </v-btn>
                
                <v-btn
                    text
                    :to="'/groups'"
                    v-if="estaActivo"
                >
                    <v-icon left dark>group</v-icon>
                    Groups
                </v-btn>

                <v-btn
                    text
                    :to="'/users'"
                    v-if="estaActivo && isAdmin"
                >
                    <v-icon left dark>face</v-icon>
                    Users
                </v-btn>

                <v-btn
                    text
                    @click="cerrarSesion()"
                    v-if="estaActivo"
                >
                    <v-icon left dark>lock</v-icon>
                    Logout
                </v-btn>
                <v-btn
                    text
                    :to="'/login'"
                    v-if="!estaActivo"
                >
                    <v-icon left dark>lock_open</v-icon>
                    Login
                </v-btn>
            </v-toolbar-items>
        </v-app-bar>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
export default {
    name: 'TopNavbar',
    data(){
        return {
            appTitle: 'Awesome App',
            sidebar: false,
            menuItems: [
                { title: 'Home', path: '/', icon: 'home' },
                { title: 'Notas', path: '/notas', icon: 'note' },
                { title: 'Groups', path: '/groups', icon: 'group' },
                { title: 'Sign Up', path: '/signup', icon: 'face' },
                { title: 'Sign In', path: '/login', icon: 'lock_open' },
            ]
        }
    },
    methods: {
        ...mapActions(['cerrarSesion', 'leerToken'])
    },
    computed: {
        ...mapGetters(['estaActivo','isAdmin'])
    },
    created(){
        this.leerToken();
    }
}
</script>